import React, { Component } from 'react'
import {Link} from "react-router-dom"
import '../stylesFrases.css'

class Frases2 extends Component{
    constructor() {
        super ();
        this.state ={ 
            frase:''
        };
    }

    myArray=[ ' De todas las cosas que llevas puestas, tu actitud es la ms importante - annimo.', 'Lo nico imposible es aquello que no intentas - Annimo..', 'Atrae lo que esperas, proyecta lo que deseas, convirtete en lo que respetas y refleja lo que admiras. - Annimo', 'La vida es un reto; vvela, siente, ama, re, llora, juega, gana, pierde, tropieza, pero siempre levntate y sigue. Annimo', 'All donde la luz no alumbre, tal vez alumbre la sombra. -Roberto Juarroz', 'Cuando subestimas lo que haces, el mundo subestima quin eres. - Oprah Winfrey', 'Confa en ti mismo... Sabes ms de lo que crees. - Benjamin Spock']

    componentDidMount(){
        this.setState({frase:this.myArray[Math.floor(Math.random()*this.myArray.length)] });
    }
    
    render(){
        return (
            <div>
                <div className="contenedor">
                        <div className="cardFrase">
                            <div className="boxTitle">
                                <h1 className="titleFrase">Frase del da.  <i class="fa fa-star" aria-hidden="true"></i></h1>
                            </div>
                            <div className="boxText">
                                <h3>
                                    {this.state.frase}
                                </h3>
                            </div>
                            <Link to="/Goals">
                            <div className="back">
                                <i class="fa fa-caret-left" aria-hidden="true"></i>
                            </div>
                            </Link>
                            <Link to="/Two">
                            <div className="continue">
                                <button className="ok">OK</button>
                            </div>
                            </Link>
                        </div>
                    </div>
            </div>
        );
    }
}


export default Frases2;
